@import url('https://fonts.googleapis.com/css?family=Roboto:300,500,700');

body{
  background-color: #f7e5d9;
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-family: Roboto, sans-serif;
  font-weight: 300;
}


#main-nav{
  border-bottom: 1px solid #dfcfc4; 
}

#main-nav ul{
  margin: 0;
  padding: 0;

}

#main-nav ul li{
  list-style-type: none;
  display: inline-block;
  padding: 1.5em 0em 0.9em 2.3em;

}


#main-nav a {
  color: #131313;
  text-decoration: none;
  font-size: 1em;
}


#main-nav a:hover {
  font-weight: 500;

}

#main-nav a:active{
  color: red;
}

#main-nav a:focus{
  font-weight: 500;
}

//WORK EDIT PAGE
.form-control{
  width: 50em;
}

.hidden{
  display:none;
}


// WORK PAGE

#work-header{
  border: 1px solid red;
}

#work-component-container{
  border: 1px solid red;
}

#work-component{
  border: 1px solid blue;
}